import React, { useState } from 'react';
import { Card, ListGroup, Badge, Button, Modal, Form } from 'react-bootstrap';
import { Row, Col } from 'react-bootstrap';

function SimilarSummaries({ summaries, onGenerateDocumentation, showRfc, generateRfc }) {
  const [showModal, setShowModal] = useState(false);
  const [selectedSummary, setSelectedSummary] = useState(null);
  const [messageText, setMessageText] = useState('');
  const [messageSent, setMessageSent] = useState(false);

  const handleConnect = (summary) => {
    setSelectedSummary(summary);
    setShowModal(true);
  };

  const handleSendMessage = () => {
    //         API
    console.log(`   ${selectedSummary.participants.join(', ')}: ${messageText}`);
    setMessageSent(true);
    
    //    3 
    setTimeout(() => {
      setMessageSent(false);
      setShowModal(false);
      setMessageText('');
    }, 3000);
  };

  if (!summaries || summaries.length === 0) {
    return (
      <Card>
        <Card.Body>
          <Card.Title>   </Card.Title>
          <Card.Text>
                    .
          </Card.Text>
        </Card.Body>
      </Card>
    );
  }

  return (
    <Card className="mb-4">
      <Card.Body>
        <Card.Title> </Card.Title>
        <Card.Subtitle className="mb-4 text-muted">
           {summaries.length}  
        </Card.Subtitle>
        
        {summaries.map(summary => (
          <Card key={summary.id} className="mb-3">
            <Card.Body>
              <Row>
                <Col md={8}>
                  <Card.Title>{summary.title}</Card.Title>
                  <Card.Text>{summary.content}</Card.Text>
                  <div className="mb-2">
                    <small className="text-muted">
                      : {summary.participants.join(', ')}
                    </small>
                  </div>
                </Col>
                <Col md={4} className="d-flex flex-column justify-content-between">
                  <div>
                    <div className="mb-2">
                      <Badge bg="info">: {(summary.similarity * 100).toFixed(0)}%</Badge>
                    </div>
                    <div className="mb-3">
                      <strong>:</strong> {summary.team}
                    </div>
                    <div className="mb-3">
                      <strong>:</strong> <a href={`mailto:${summary.contact}`}>{summary.contact}</a>
                    </div>
                  </div>
                  <Button 
                    variant="outline-dark" 
                    size="sm"
                    onClick={() => window.open(`mailto:${summary.contact}?subject=   "${summary.title}"`, '_blank')}
                  >
                      
                  </Button>
                </Col>
              </Row>
            </Card.Body>
          </Card>
        ))}
        
        <div className="mt-4 text-center">
          {!generateRfc || (generateRfc && showRfc) ? (
            <Button 
              variant="dark" 
              onClick={onGenerateDocumentation}
            >
                 
            </Button>
          ) : (
            <div className="text-muted">
                RFC...
            </div>
          )}
        </div>
      </Card.Body>
      
      <Modal show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>  </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {messageSent ? (
            <div className="text-center py-4">
              <div className="mb-3"></div>
              <h4> !</h4>
              <p>        .</p>
            </div>
          ) : (
            <>
              <p>
                    "{selectedSummary?.title}":
                <br />
                <strong>{selectedSummary?.participants.join(', ')}</strong>
              </p>
              <Form>
                <Form.Group>
                  <Form.Control
                    as="textarea"
                    rows={4}
                    value={messageText}
                    onChange={(e) => setMessageText(e.target.value)}
                    placeholder="  ..."
                  />
                </Form.Group>
              </Form>
            </>
          )}
        </Modal.Body>
        {!messageSent && (
          <Modal.Footer>
            <Button variant="secondary" onClick={() => setShowModal(false)}>
              
            </Button>
            <Button 
              variant="primary" 
              onClick={handleSendMessage}
              disabled={!messageText.trim()}
            >
              
            </Button>
          </Modal.Footer>
        )}
      </Modal>
    </Card>
  );
}

export default SimilarSummaries; 